'use client';

import { useState } from 'react';
import { Users, UserPlus, Loader2 } from 'lucide-react';
import clsx from 'clsx';



export default function TeamOverview({ teamMembers = [], onCreate = async () => {} }) {

  return (
    <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
      {teamMembers.length > 0 ? teamMembers.map((member) => {
        const tasks = Array.isArray(member.tasks) ? member.tasks.length : member.taskCount || 0;
        const capacity = member.capacity || 5;
        const workload = capacity ? Math.min(100, Math.round((tasks / capacity) * 100)) : 0;
        
        const getProgressColor = () => {
          if (workload === 100) return 'bg-red-500';
          if (workload >= 80) return 'bg-orange-500';
          if (workload >= 60) return 'bg-yellow-500';
          return 'bg-green-500';
        };

        const initials = (name = '') => {
          const [first = '', second = ''] = name.split(' ');
          return `${first.charAt(0)}${second.charAt(0)}`.toUpperCase() || name.charAt(0).toUpperCase();
        };

        return (
          <div key={member._id} className="bg-white rounded-2xl p-6 border border-slate-200">
            <div className="flex items-center gap-4 mb-4">
              <div className="w-16 h-16 rounded-full bg-slate-200 flex items-center justify-center text-lg font-semibold text-slate-700">
                {initials(member.name)}
              </div>
              <div className="flex-1">
                <h3 className="font-semibold text-slate-900">{member.name}</h3>
                <p className="text-slate-500 text-sm">{member.role}</p>
              </div>
            </div>
            
            <div className="mb-4">
              <div className="flex items-center justify-between mb-2">
                <span className="text-sm text-slate-600">{tasks} tasks assigned</span>
                <span className="text-sm font-medium">{tasks}/{capacity} Tasks</span>
              </div>
              <div className="w-full bg-slate-200 rounded-full h-2">
                <div 
                  className={`h-2 rounded-full ${getProgressColor()}`}
                  style={{ width: `${Math.min(workload, 100)}%` }}
                ></div>
              </div>
            </div>
          </div>
        );
      }) : (
        <div className="col-span-full text-center py-12 text-slate-500">
          No team members yet. Add team members to track workload.
        </div>
      )}
    </div>
  );
}
